.fav-tracker {
	background-color: gray;
	border-radius: 5px;
	padding: 5px 10px;
	color: white;
	font-weight: 500;
	margin-left: 5px;
}
.fav {
	display: inline-block;
	margin-right: 50px;
}
.fav-bar {
	position: absolute;
	background-color: white;
	border-radius: 5px;
	text-align: center;
	padding: 10px;
	box-shadow: 5px 5px 5px gray;
	display: flex;
}
.fav-header {
	margin-top: 0;
	margin-right: 10px;
	align-self: center;
}
.fav-item {
	display: flex;
	flex-direction: column;
	border-color: #4BB3A5;
	margin: 5px;
	padding: 10px;
	border-radius: 5px;
	border-width: thin;
	border-style: solid;
	align-items: center;
}
.delete-button {
	margin: 10px;
	background-color: white;
	border-color: #4BB3A5;
	border-radius: 5px;
}
.hideButton {
	background-color: white;
	border-width: thin;
	border-color: red;
	border-radius: 5px;
	color: red;
	padding: 5px;
}
.hide {
	display: none;
}
.show-hide {
	margin-right: 5px;
	background-color: #4BB3A5;
	color: white;
	border-radius: 5px;
	border-width: 0;
	padding: 5px;
}
.fav-thumbnail {
	width: 50px;
	height: 50px;
}


